@use '../variables' as *;

.btn {
    display: inline-block;
    background: var(--primary-color);
    color: var(--primary-color-text);
    padding: 0.5rem 1rem;
    border-radius: 5px;
    text-decoration: none;
    margin-top: 1rem;
    margin-right: 0.5rem;
    transition: background-color $transition-medium;
  
    &:hover {
      background: var(--primary-hover);
    }
  }