@use '../variables' as *;

.footer {
    background: var(--fg-color);
    color: var(--text-color);
    padding: 3rem 0;
    text-align: center;
    margin-top: 4rem;
    box-shadow: 0 -2px 5px var(--shadow-color);
  
    .scroll-top {
      background: var(--primary-color);
      color: white;
      border: none;
      padding: 1rem;
      border-radius: 50%;
      cursor: pointer;
      margin-bottom: 2rem;
      transition: background-color $transition-medium;
  
      &:hover {
        background: var(--primary-hover);
      }
    }
  }